import React from "react";
import { StyledMessage } from "./Message.styled";


const Message = ({ name, time, avi, messageContent, messageClass }) => {
  return (
    <StyledMessage>
        <div className={messageClass}>
            <img
              src={avi}
              className="small-avatar"
            />
            <div className="upper-message">
              <div className="message-name-time">
                <div className="name">{name}</div>
                <div className="message-time">{time}</div>
              </div>
              <div className="message-text">{messageContent}</div>
            </div>
        </div>
    </StyledMessage>
  );
};

export default Message;


