import React, { useReducer, useRef, useState } from "react";
import { StyledContainerMessage } from "./MessageContainer.styled";

import Message from "../Message/Message";

import arrow from "./assets/Arrow.svg";
import clip from "./assets/clip.svg";
import avi from './assets/am.jpg'

/********************* */
//adding in reducer
/********************* */

const reducer = (state, action) => {
  switch (action.type) {
    case "add":
      return [
        ...state,
        {
          id: state.length,
          name: action.name
        }
      ];

    default:
      return state;
  }
};

let responseText = 0;
  
/********************* */
//adding in reducer
/********************* */



const MessageContainer = () => {
/********************* */
//adding in reducer
/********************* */


  const inputRef = useRef();
  const [items, dispatch] = useReducer(reducer, []);

  const [steps, setSteps] = useState(0);
  function increment() {
    setSteps((steps) => steps + 1);
    if (steps >= 2) {
      setSteps(0);
    }
  }

  const responseText = [
    "Great, I can absolutely get you an offer by then. Any other details I should know?", 
    "Perfect. I'll talk to you early next week.", 
    "Ut id velit a metus porta sagittis. Integer lacus nulla, tincidunt ut felis in, viverra commodo est. Duis ultrices consequat risus ac interdum."];

  function handleSubmit(e) {
    e.preventDefault();
    dispatch({
      type: "add",
      name: inputRef.current.value
    });
    inputRef.current.value = "";
  }

/********************* */
//adding in reducer
/********************* */


  return (
    <StyledContainerMessage>
      <div class="container center-box">
        <div class="message-container">
          <div class="inner-message-container" id="messages">
          <Message 
            messageClass={"message-wrapper"}
            name={"Gillian Vanderbilt"} 
            time={"• Yesterday"} 
            avi={"https://images.crexi.com/users/77872/c1da142587284cf3a18fb696aeb7dc66_164x164.jpg"} 
            messageContent={"Hey Jon, I'm very interested in your listing for the 7-11 on Main. When are you expecting offers by?"}
              />
            {items.map((item, index) => (
              <>
            <Message
             messageClass={"message-wrapper"}
             name={"Jon Fletcher"} 
             time={"• Just now"} 
             avi={avi}
             messageContent={item.name}/>
            <Message 
              messageClass={"message-wrapper delay"}
              name={"Gillian Vanderbilt"} 
              time={"• Just now"} 
              avi={"https://images.crexi.com/users/77872/c1da142587284cf3a18fb696aeb7dc66_164x164.jpg"} 
              messageContent={responseText[(steps, index)]}
              />
            </>
            ))}
          </div>

          

          <div class="write">
          <form onSubmit={handleSubmit}>
            <div class="input-container">
              <div class="icon white">
                <img src={clip} />
              </div>
              <div class="type-container">
                <textarea
                  className="message"
                  type="text"
                  name="sentence"
                  placeholder="Type your message here..."
                  ref={inputRef} 
                  onSubmit={increment}
                ></textarea>
                <button 
                className="message-button"
                onClick={increment}
                >
                  <img src={arrow} width="16px" height="16px" />
                </button>
              </div>
            </div>
            </form>
          </div>
        </div>
      </div>
    </StyledContainerMessage>
  );
};

export default MessageContainer;
